"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import Link from "next/link"
import { ChevronLeft } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import { Progress } from "@/components/ui/progress"
import { Badge } from "@/components/ui/badge"
import { toast } from "sonner"

// Mock data - replace with your actual data fetching logic
const fetchCampaign = async (id) => {
  // Simulate API call
  return {
    id,
    name: "Community Garden Project",
    description: "Vote on which plants to include in our neighborhood garden project.",
    votingType: "proposal", // "proposal" or "candidate"
    resultType: "one-winner", // "one-winner" or "rank-based"
    startTime: new Date(Date.now() - 1000 * 60 * 60).toISOString(), // 1 hour ago
    endTime: new Date(Date.now() + 1000 * 60 * 60 * 24).toISOString(), // 1 day from now
    proposals: [
      { id: 1, content: "Native wildflowers", voteCount: 12 },
      { id: 2, content: "Vegetable garden", voteCount: 8 },
      { id: 3, content: "Herb garden", voteCount: 5 },
      { id: 4, content: "Fruit trees", voteCount: 7 },
    ],
    candidates: [
      {
        id: 1,
        name: "Jane Smith",
        reason: "Experienced gardener with 10 years of community garden management",
        voteCount: 15,
      },
      { id: 2, name: "John Doe", reason: "Landscape architect with sustainable design experience", voteCount: 12 },
      { id: 3, name: "Alice Johnson", reason: "Master gardener with focus on native plants", voteCount: 8 },
    ],
  }
}

export default function VotePage() {
  const params = useParams()
  const router = useRouter()
  const campaignId = params.id

  const [campaign, setCampaign] = useState(null)
  const [loading, setLoading] = useState(true)
  const [selectedOption, setSelectedOption] = useState(null)
  const [hasVoted, setHasVoted] = useState(false)
  const [votingEnded, setVotingEnded] = useState(false)

  useEffect(() => {
    const loadCampaign = async () => {
      try {
        setLoading(true)
        const data = await fetchCampaign(campaignId)
        setCampaign(data)

        // Check if voting has ended
        const now = new Date()
        const endTime = new Date(data.endTime)
        setVotingEnded(now >= endTime)

        // Check if user has voted (in a real app, this would be from your blockchain or database)
        const votedStatus = localStorage.getItem(`voted_${campaignId}`)
        setHasVoted(votedStatus === "true")
      } catch (error) {
        console.error("Failed to load campaign:", error)
        toast.error("Failed to load voting details")
      } finally {
        setLoading(false)
      }
    }

    loadCampaign()
  }, [campaignId])

  const handleVote = () => {
    if (!selectedOption) {
      toast.error("Please select an option to vote")
      return
    }

    // In a real app, you would submit the vote to the blockchain
    toast.success("Vote submitted successfully!")

    // Update local state
    setHasVoted(true)
    localStorage.setItem(`voted_${campaignId}`, "true")

    // Update vote count in UI
    if (campaign.votingType === "proposal") {
      const updatedProposals = campaign.proposals.map((proposal) =>
        proposal.id === selectedOption ? { ...proposal, voteCount: proposal.voteCount + 1 } : proposal,
      )
      setCampaign({ ...campaign, proposals: updatedProposals })
    } else {
      const updatedCandidates = campaign.candidates.map((candidate) =>
        candidate.id === selectedOption ? { ...candidate, voteCount: candidate.voteCount + 1 } : candidate,
      )
      setCampaign({ ...campaign, candidates: updatedCandidates })
    }
  }

  if (loading) {
    return (
      <div className="container max-w-4xl mx-auto py-8">
        <div className="flex items-center justify-center h-64">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto"></div>
            <p className="mt-4 text-muted-foreground">Loading voting details...</p>
          </div>
        </div>
      </div>
    )
  }

  if (!campaign) {
    return (
      <div className="container max-w-4xl mx-auto py-8">
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">Campaign Not Found</h2>
          <p className="text-muted-foreground mb-6">
            The campaign you're looking for doesn't exist or couldn't be loaded.
          </p>
          <Button asChild>
            <Link href="/">Back to Home</Link>
          </Button>
        </div>
      </div>
    )
  }

  const options = campaign.votingType === "proposal" ? campaign.proposals : campaign.candidates
  const totalVotes = options.reduce((sum, option) => sum + option.voteCount, 0)

  return (
    <div className="container max-w-4xl mx-auto py-8">
      <div className="mb-6">
        <Link
          href={`/campaign/${campaignId}`}
          className="flex items-center text-muted-foreground hover:text-foreground transition-colors"
        >
          <ChevronLeft className="h-4 w-4 mr-1" />
          Back to campaign details
        </Link>
      </div>

      <Card className="mb-6">
        <CardHeader>
          <div className="flex justify-between items-start">
            <div>
              <CardTitle className="text-2xl">{campaign.name}</CardTitle>
              <CardDescription className="mt-2">{campaign.description}</CardDescription>
            </div>
            <Badge variant={votingEnded ? "destructive" : "default"}>
              {votingEnded ? "Voting Ended" : "Voting Active"}
            </Badge>
          </div>
        </CardHeader>

        <CardContent>
          <h3 className="text-lg font-medium mb-4">
            {campaign.votingType === "proposal" ? "Proposals" : "Candidates"}
          </h3>

          {votingEnded ? (
            <div className="space-y-6">
              {options.map((option) => (
                <div key={option.id} className="space-y-2">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="font-medium">
                        {campaign.votingType === "proposal" ? option.content : option.name}
                      </h4>
                      {campaign.votingType === "candidate" && (
                        <p className="text-sm text-muted-foreground">{option.reason}</p>
                      )}
                    </div>
                    <div className="text-right">
                      <span className="font-medium">{option.voteCount}</span>
                      <span className="text-muted-foreground text-sm ml-1">
                        ({totalVotes > 0 ? Math.round((option.voteCount / totalVotes) * 100) : 0}%)
                      </span>
                    </div>
                  </div>
                  <Progress value={totalVotes > 0 ? (option.voteCount / totalVotes) * 100 : 0} />
                </div>
              ))}
            </div>
          ) : hasVoted ? (
            <div className="space-y-6">
              <div className="bg-muted p-4 rounded-md text-center mb-4">
                <p className="font-medium">Thank you for voting!</p>
                <p className="text-sm text-muted-foreground">Your vote has been recorded.</p>
              </div>

              {options.map((option) => (
                <div key={option.id} className="space-y-2">
                  <div className="flex items-center justify-between">
                    <div>
                      <h4 className="font-medium">
                        {campaign.votingType === "proposal" ? option.content : option.name}
                      </h4>
                      {campaign.votingType === "candidate" && (
                        <p className="text-sm text-muted-foreground">{option.reason}</p>
                      )}
                    </div>
                    <div className="text-right">
                      <span className="font-medium">{option.voteCount}</span>
                      <span className="text-muted-foreground text-sm ml-1">
                        ({totalVotes > 0 ? Math.round((option.voteCount / totalVotes) * 100) : 0}%)
                      </span>
                    </div>
                  </div>
                  <Progress value={totalVotes > 0 ? (option.voteCount / totalVotes) * 100 : 0} />
                </div>
              ))}
            </div>
          ) : (
            <RadioGroup value={selectedOption} onValueChange={setSelectedOption}>
              <div className="space-y-4">
                {options.map((option) => (
                  <div
                    key={option.id}
                    className={`flex items-start space-x-3 border rounded-lg p-4 transition-colors ${
                      selectedOption === option.id ? "bg-muted border-primary" : ""
                    }`}
                  >
                    <RadioGroupItem value={option.id} id={`option-${option.id}`} className="mt-1" />
                    <div className="flex-1">
                      <Label htmlFor={`option-${option.id}`} className="text-base font-medium cursor-pointer">
                        {campaign.votingType === "proposal" ? option.content : option.name}
                      </Label>
                      {campaign.votingType === "candidate" && (
                        <p className="text-sm text-muted-foreground mt-1">{option.reason}</p>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </RadioGroup>
          )}
        </CardContent>

        <CardFooter>
          {!votingEnded && !hasVoted ? (
            <Button className="w-full" onClick={handleVote} disabled={!selectedOption}>
              Cast Vote
            </Button>
          ) : (
            <div className="w-full text-center text-muted-foreground">
              {votingEnded ? "Voting has ended" : "You have already voted"}
            </div>
          )}
        </CardFooter>
      </Card>
    </div>
  )
}

